.button-wrapper {
  height: 4em;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: lighter;
  font-size: 1.4em;
  background-color:#008EC3;
  color: rgb(255, 239, 239);
  flex: 1;
  outline: 1px solid #8A2AE3;
}
.button-wrapper:hover{
  background-color:rgb(27, 163, 212);
  cursor: pointer;
}

.operator {
  background-color: #F68311;
  color: #f4f4f4;;
}
.operator:hover{
  background-color: rgb(206, 114, 23);
  color: #fff;
}