.clear-btn {
  height: 2.5em;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: lighter;
  font-weight: 500;
  font-size: 1.4em;
  background-color: rgb(247, 41, 48);
  color: #fff;
  flex: 1;
  outline: 1px solid #888;
}

.clear-btn:hover {
  background-color: rgb(255, 67, 73);
  outline: 1px solid #888;
  color: #f4f4f4;
  cursor: pointer;
}
